body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 25px;
  cursor: default;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

footer {
  text-align: right;
  font-size: 1rem;
  padding: 1%;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  color: #000;
}

.bold {
  font-weight: bold;
}

.city-container,
.display-flex {
  display: flex;
}

.display-block {
  display: block;
}

.flex-wrap {
  flex-wrap: wrap;
}

.align-center {
  align-items: center;
}

.modal .modal-header,
footer {
  background-color: #185a9d;
  color: #fff;
}

.modal .mobal-body {
  padding: 2rem;
}

.navi,
.jumboGrad {
  background-image: linear-gradient(
    to right,
    #43cea2 0%,
    #185a9d 51%,
    #43cea2 100%
  );
}
.jumboGrad {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.jumbotron {
  object-fit: cover;
  padding: 2rem;
}

.card {
  font-size: 20px;
  color: white;
}

.card-text {
  line-height: 3rem;
}

.royal {
  background: #141e30;
  background: -webkit-linear-gradient(to top, #243b55, #141e30);
  background: linear-gradient(to top, #243b55, #141e30);
}

.royal .card-header {
  color: #97b1ce;
}

.list-group-item {
  background-color: transparent;
  color: white;
}

.navbar .nav-link {
  text-align: left;
  font-size: 20px;
  padding: 1em !important;
  transition: 0.5s;
  border-radius: 0.28571429rem;
  border: 1px solid transparent;
}

.navbar-collapse {
  justify-content: flex-end;
}

.navi,
.jumboGrad {
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  display: block;
}

.navbar .nav-link:hover,
.navbar-dark .navbar-nav .nav-link:hover {
  background-position: right center;
  box-shadow: 0 0 10px #eee;
  border-color: #fff;
  color: #fff;
}

.image-cropper {
  max-width: 500px;
  max-height: 300px;
  position: relative;
  overflow: hidden;
  border-radius: 5%;
  padding-left: 20px;
}

.city-pic {
  display: inline;
  margin: 0 auto;
  margin-left: -25%;
  height: 100%;
  width: auto;
}

.mt-10,
.ui.button.mt-10 {
  margin-top: 10px;
}

.mb-10 {
  margin-bottom: 10px;
}

table:hover {
  cursor: default;
}

td {
  text-align: center !important;
}

.sr-only {
  visibility: hidden;
}

.close {
  background: 0 0;
  border: 0;
  border-radius: 0.25rem;
  display: inline-block;
  font-size: 24px;
  max-width: 30px;
  max-height: 30px;
}

.close span {
  background: transparent;
  color: white;
}

.alert .close {
  float: right;
  margin: -5px -25px;
}

.modal .form-group {
  margin-bottom: 1.7rem;
  font-size: 1.1rem;
}

.modal .form-control {
  padding: 10px 15px;
  font-size: 1.1rem;
}

.container .ui.label,
table.table thead th:nth-child(n + 2) {
  border: 2px solid;
  border-color: #29899f;
  background-color: white;
}

th {
  color: white;
  background-color: #48979c !important;
  border: 0px !important;
}
table.table {
  font-size: 1.1rem;
}

table.table thead th:nth-child(n + 2) {
  text-align: center;
}

table.table tr td:nth-child(n + 2) {
  font-weight: bold;
  text-align: right;
}

table.table-striped > tbody > tr:nth-of-type(odd),
table.table-striped > tbody > tr:nth-of-type(even):hover {
  --bs-table-accent-bg: #b2e9d538;
}
.scores,
.ui.fluid.card {
  box-shadow: 0px 1px 7px 7px rgba(61, 109, 99, 0.2);
  transition: 0.3s;
  padding: 1rem;
  text-align: center;
  margin-bottom: 1.5rem;
  border-radius: 0.28571429rem;
  line-height: 1.2;
}

.data {
  border: 4px solid;
  border-image: linear-gradient(45deg, #43cea28e, #185b9d94) 10;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  padding: 1.5rem;
  margin-bottom: 1rem;
  text-align: center;
  border-radius: 0.28571429rem;
}

.CityChartName {
  padding: 1rem;
}

.delete {
  margin-left: 1rem;
}

footer {
  text-align: center;
}

.autoComplete {
  padding: 50px 20px;
}

.about-me .container {
  padding: 0;
}

.dropdown-menu {
  max-height: 75px !important;
}

.user-input {
  padding: 5px 15px;
  width: 100%;
  position: relative;
}

.home-search form {
  position: relative;
}

.suggestions-list {
  background-color: rgb(2 51 95 / 79%);
  width: calc(100% - 6rem);
  position: absolute;
  list-style: none;
  padding: 0;
  max-height: 150px;
  overflow-y: auto;
  font-size: 18px;
  transition: 0.5s;
  box-shadow: 0 0 10px rgb(37, 37, 37);
}
.suggestion {
  padding: 15px;
  color: rgb(185, 184, 184);
  cursor: pointer;
}
.suggestion:hover {
  background-color: rgb(0 190 133 / 65%);
  color: rgb(224, 224, 224);
  font-weight: bold;
  transition: 0.5s;
  box-shadow: 0 0 10px #eee;
}
.selected {
  background-color: rgba(238, 238, 238, 0.1);
  color: rgb(238, 223, 223);
  border: solid rgba(255, 255, 255, 0.397) 2px;
}

.savedChart {
  color: black !important;
  font-size: 20px;
  padding: 10px !important;
  line-height: 2rem;
}
.nav-item .active {
  background-color: rgb(0, 165, 143) !important;
  color: White !important;
  font-weight: bold;

  transition: 0.1s ease-in;
}

.jumbotron .ui.card > .extra,
.jumbotron .ui.card .meta {
  color: rgba(0, 0, 0, 0.68);
}

.jumbotron .ui.card > .extra i {
  color: #43cea2;
}

.extra.content,
.text-left,
.jumboGrad .text-left {
  text-align: left;
}

.autoComplete form {
  position: relative;
}

ul.suggestions-list {
  position: absolute;
  width: calc(100% - 6rem);
  top: 100%;
}

.scores .nav.nav-pills {
  border-radius: 0.28571429rem;
  line-height: 1.2;
  background-color: #43cea2;
  margin: 1px 3px;
}

.scores .nav.nav-pills .nav-link {
  color: #fff;
}

.ui.container:empty {
  display: none;
}

.my-5 .card {
  flex-basis: 30%;
  margin: 0 0 20px 0 !important;
  width: 100%;
  justify-content: flex-start;
  padding: 0;
}

.my-5 .card-header {
  display: flex;
  align-items: center;
}

.my-5 .card-header svg {
  min-width: 30px;
  min-height: 30px;
  flex-basis: 40%;
  color: #43cea2;
}

.my-5 .card-header .card-title {
  font-size: 1.6rem;
  line-height: 1;
  margin: 0;
  color: #185a9d;
}

.my-5 .card-body {
  align-items: center;
  display: flex;
  color: #185a9d;
  padding: 1.2rem 1.5rem;
}

.no-suggestions {
  position: absolute;
  top: calc(100% + 10px);
}

.font-normal {
  font-weight: normal;
}

.navbar-brand .ui.tiny.logo-image,
.home-logo {
  max-width: 30px;
  margin-right: 10px;
}

/* Media queries */
@media screen and (max-width: 768px) {
  .my-5 .card {
    flex-basis: 100%;
  }

  .jumboGrad .jumbo,
  .jumboGrad .jumbo .p-5 {
    padding: 0.5rem !important;
  }
}
